"use client";

import { useState, useEffect, useRef } from "react";
import { getConversations, searchUsers, createConversation, getMessages, sendMessage } from "@/lib/api-client";
import type { Conversation, User, Message, Discussion } from "@/lib/api-client";

export default function ChatLayout() {
  const [conversations, setConversations] = useState<Conversation[]>([]);
  const [selectedConvId, setSelectedConvId] = useState<number | null>(null);
  const [messages, setMessages] = useState<Message[]>([]);
  const [discussion, setDiscussion] = useState<Discussion | null>(null);
  const [showNewConvModal, setShowNewConvModal] = useState(false);
  const [searchQuery, setSearchQuery] = useState("");
  const [newConvSearch, setNewConvSearch] = useState("");
  const [searchedUsers, setSearchedUsers] = useState<User[]>([]);
  const [messageInput, setMessageInput] = useState("");
  const [loading, setLoading] = useState(true);
  const [sendingMessage, setSendingMessage] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // Fetch conversations on mount
  useEffect(() => {
    fetchConversations();
  }, []);

  // Fetch messages when conversation is selected
  useEffect(() => {
    if (selectedConvId) {
      fetchMessages(selectedConvId);
    }
  }, [selectedConvId]);

  // Auto-scroll to bottom when messages change
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  const fetchConversations = async () => {
    try {
      setLoading(true);
      const convs = await getConversations();
      setConversations(convs);
      if (convs.length > 0 && !selectedConvId) {
        setSelectedConvId(convs[0].id);
      }
    } catch (err) {
      setError("Failed to load conversations");
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  const fetchMessages = async (convId: number) => {
    try {
      const { discussion, messages } = await getMessages(convId);
      setDiscussion(discussion);
      setMessages(messages);
    } catch (err) {
      setError("Failed to load messages");
      console.error(err);
    }
  };

  const handleSearchUsers = async (query: string) => {
    if (query.length < 2) {
      setSearchedUsers([]);
      return;
    }
    try {
      const users = await searchUsers(query);
      setSearchedUsers(users);
    } catch (err) {
      console.error("Search failed:", err);
    }
  };

  const handleStartConversation = async (userId: number) => {
    try {
      setSendingMessage(true);
      const conv = await createConversation(userId);
      
      // Refresh conversations
      await fetchConversations();
      
      // Select the new conversation
      setSelectedConvId(conv.id);
      setShowNewConvModal(false);
      setNewConvSearch("");
      setSearchedUsers([]);
    } catch (err) {
      setError("Failed to create conversation");
      console.error(err);
    } finally {
      setSendingMessage(false);
    }
  };

  const handleSendMessage = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!messageInput.trim() || !selectedConvId || sendingMessage) return;

    try {
      setSendingMessage(true);
      const newMsg = await sendMessage(selectedConvId, {
        content: messageInput.trim(),
      });
      
      // Add message to state
      setMessages([...messages, newMsg]);
      setMessageInput("");
      
      // Refresh conversations to update "last message"
      await fetchConversations();
    } catch (err) {
      setError("Failed to send message");
      console.error(err);
    } finally {
      setSendingMessage(false);
    }
  };

  const filteredConversations = conversations.filter(
    (conv) =>
      conv.otherUser.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      conv.otherUser.username.toLowerCase().includes(searchQuery.toLowerCase())
  );

  const selectedConv = conversations.find((c) => c.id === selectedConvId);

  const formatTime = (dateString: string) => {
    const date = new Date(dateString);
    const now = new Date();
    const diffMs = now.getTime() - date.getTime();
    const diffMins = Math.floor(diffMs / 60000);
    const diffHours = Math.floor(diffMs / 3600000);
    const diffDays = Math.floor(diffMs / 86400000);

    if (diffMins < 1) return "now";
    if (diffMins < 60) return `${diffMins}m ago`;
    if (diffHours < 24) return `${diffHours}h ago`;
    if (diffDays < 7) return `${diffDays}d ago`;
    
    return date.toLocaleDateString();
  };

  return (
    <div className="h-screen flex bg-gray-100 overflow-hidden">
      {/* Left Sidebar: Conversations */}
      <div className="w-full md:w-64 bg-white border-r border-gray-200 overflow-y-auto flex flex-col">
        {/* Header */}
        <div className="p-4 border-b border-gray-200">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-lg font-bold text-gray-900">Messages</h2>
            <button
              onClick={() => setShowNewConvModal(true)}
              className="bg-indigo-600 text-white p-2 rounded-lg hover:bg-indigo-700 transition"
              title="New conversation"
            >
              <svg
                className="w-5 h-5"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M12 4v16m8-8H4"
                />
              </svg>
            </button>
          </div>

          {/* Search */}
          <input
            type="text"
            placeholder="Search conversations..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="w-full px-3 py-2 border border-gray-300 rounded-lg text-sm focus:outline-none focus:ring-2 focus:ring-indigo-500"
          />
        </div>

        {/* Conversations List */}
        <div className="flex-1 overflow-y-auto divide-y divide-gray-200">
          {loading ? (
            <div className="p-4 text-center text-gray-500">Loading...</div>
          ) : filteredConversations.length > 0 ? (
            filteredConversations.map((conv) => (
              <button
                key={conv.id}
                onClick={() => setSelectedConvId(conv.id)}
                className={`w-full text-left px-4 py-3 transition ${
                  selectedConvId === conv.id
                    ? "bg-indigo-50 border-l-4 border-indigo-600"
                    : "hover:bg-gray-50"
                }`}
              >
                <p className="font-semibold text-gray-900 text-sm">
                  {conv.otherUser.name} {conv.otherUser.surname}
                </p>
                <p className="text-xs text-gray-500">@{conv.otherUser.username}</p>
                <p className="text-xs text-gray-600 truncate mt-1">
                  {conv.lastMessage?.content || "No messages yet"}
                </p>
                <p className="text-xs text-gray-400 mt-1">
                  {conv.lastMessage ? formatTime(conv.lastMessage.createdAt) : ""}
                </p>
              </button>
            ))
          ) : (
            <div className="p-4 text-center text-gray-500 text-sm">
              No conversations found
            </div>
          )}
        </div>
      </div>

      {/* Right Content Area: Messages */}
      <div className="hidden md:flex flex-1 flex-col bg-white">
        {selectedConv && discussion ? (
          <>
            {/* Chat Header */}
            <div className="border-b border-gray-200 bg-white px-6 py-4 flex items-center justify-between">
              <div>
                <h3 className="text-lg font-semibold text-gray-900">
                  {selectedConv.otherUser.name} {selectedConv.otherUser.surname}
                </h3>
                <p className="text-xs text-gray-500">@{selectedConv.otherUser.username}</p>
              </div>
            </div>

            {/* Messages Area */}
            <div className="flex-1 overflow-y-auto p-6 space-y-4">
              {messages.length === 0 ? (
                <div className="text-center text-gray-500 py-8">
                  <p>Start the conversation!</p>
                </div>
              ) : (
                messages.map((msg) => (
                  <div
                    key={msg.id}
                    className={`flex ${
                      msg.senderId === selectedConv.otherUser.id
                        ? "justify-start"
                        : "justify-end"
                    }`}
                  >
                    <div
                      className={`max-w-xs rounded-lg px-4 py-2 ${
                        msg.senderId === selectedConv.otherUser.id
                          ? "bg-gray-200 text-gray-900"
                          : "bg-indigo-600 text-white"
                      }`}
                    >
                      {msg.content && (
                        <p className="text-sm break-words">{msg.content}</p>
                      )}
                      {msg.mediaUrl && (
                        <div className="mt-2">
                          {msg.mediaType?.startsWith("image") ? (
                            <img
                              src={msg.mediaUrl}
                              alt="Message attachment"
                              className="max-w-xs rounded"
                            />
                          ) : (
                            <a
                              href={msg.mediaUrl}
                              target="_blank"
                              rel="noopener noreferrer"
                              className="text-xs underline"
                            >
                              Download attachment
                            </a>
                          )}
                        </div>
                      )}
                      <p className="text-xs mt-1 opacity-70">
                        {formatTime(msg.createdAt)}
                      </p>
                    </div>
                  </div>
                ))
              )}
              <div ref={messagesEndRef} />
            </div>

            {/* Input Area */}
            <div className="border-t border-gray-200 bg-white px-6 py-4">
              {error && (
                <div className="mb-3 p-2 bg-red-100 text-red-700 text-sm rounded">
                  {error}
                </div>
              )}
              <form onSubmit={handleSendMessage} className="flex items-center gap-3">
                <input
                  type="text"
                  placeholder="Type a message..."
                  value={messageInput}
                  onChange={(e) => setMessageInput(e.target.value)}
                  disabled={sendingMessage}
                  className="flex-1 px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500 disabled:bg-gray-100"
                />
                <button
                  type="submit"
                  disabled={sendingMessage || !messageInput.trim()}
                  className="bg-indigo-600 text-white px-4 py-2 rounded-lg hover:bg-indigo-700 transition disabled:bg-gray-400"
                >
                  {sendingMessage ? "Sending..." : "Send"}
                </button>
              </form>
            </div>
          </>
        ) : (
          <div className="flex-1 flex items-center justify-center text-center">
            <div>
              <svg
                className="w-16 h-16 text-gray-300 mx-auto mb-4"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={1.5}
                  d="M8 12h.01M12 12h.01M16 12h.01M21 12c0 4.418-4.03 8-9 8a9.863 9.863 0 01-4.255-.949L3 20l1.395-3.72C3.512 15.042 3 13.574 3 12c0-4.418 4.03-8 9-8s9 3.582 9 8z"
                />
              </svg>
              <p className="text-gray-500 text-lg">
                {loading ? "Loading..." : "Select a conversation to start"}
              </p>
            </div>
          </div>
        )}
      </div>

      {/* New Conversation Modal */}
      {showNewConvModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg shadow-lg w-full max-w-md mx-4">
            {/* Modal Header */}
            <div className="border-b border-gray-200 px-6 py-4 flex items-center justify-between">
              <h3 className="text-lg font-semibold text-gray-900">
                Start New Conversation
              </h3>
              <button
                onClick={() => {
                  setShowNewConvModal(false);
                  setNewConvSearch("");
                  setSearchedUsers([]);
                }}
                className="text-gray-400 hover:text-gray-600"
              >
                <svg
                  className="w-6 h-6"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M6 18L18 6M6 6l12 12"
                  />
                </svg>
              </button>
            </div>

            {/* Search Input */}
            <div className="p-6 border-b border-gray-200">
              <input
                type="text"
                placeholder="Search by name or username..."
                value={newConvSearch}
                onChange={(e) => {
                  setNewConvSearch(e.target.value);
                  handleSearchUsers(e.target.value);
                }}
                autoFocus
                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-indigo-500"
              />
            </div>

            {/* Users List */}
            <div className="max-h-96 overflow-y-auto divide-y divide-gray-200">
              {searchedUsers.length > 0 ? (
                searchedUsers.map((user) => (
                  <button
                    key={user.id}
                    onClick={() => handleStartConversation(user.id)}
                    disabled={sendingMessage}
                    className="w-full text-left px-6 py-4 hover:bg-gray-50 transition flex items-center justify-between disabled:opacity-50"
                  >
                    <div>
                      <p className="font-semibold text-gray-900 text-sm">
                        {user.name} {user.surname}
                      </p>
                      <p className="text-xs text-gray-500">@{user.username}</p>
                    </div>
                    <svg
                      className="w-5 h-5 text-gray-400"
                      fill="none"
                      stroke="currentColor"
                      viewBox="0 0 24 24"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth={2}
                        d="M9 5l7 7-7 7"
                      />
                    </svg>
                  </button>
                ))
              ) : newConvSearch.length > 0 ? (
                <div className="p-6 text-center text-gray-500 text-sm">
                  No users found
                </div>
              ) : (
                <div className="p-6 text-center text-gray-500 text-sm">
                  Start typing to search users...
                </div>
              )}
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
